import { RESULTS as ACTIONS } from './../actions/constants/Actions.constants.js';

const initialState = {
				items :  [],
				error :  null
			};

export default function results ( state = initialState, action = { type : '' } ) {
	switch ( action.type ) {
		case ACTIONS.RESET_RESULTS:
			return initialState;
		case ACTIONS.RESULTS_FETCHED:
			return {
				...state,
				items :  action.result.items,
				error :  null
			};
		case ACTIONS.RESULTS_FETCHED_ERROR:
			return {
				...state,
				error : action.error
			};
		default:
			return state;
	}
}



