import { h, Component } from 'preact';
import { connect } from 'preact-redux';

import ResultListItem from './ResultListItem.preact';

@connect(
	state => state.result
)
class ResultList extends Component {

	// constructor ( { items } ) {
	// 	super();

	// 	this.state = { items };
	// }

	// componentWillReceiveProps ( { items } ) {
	// 	this.setState( { items } );
	// }

	/**
	 * TODOOOOO
	 * Prevent from rerender every time it is opened / closed without receiving new props
	 */
	render ( { items } ) {
		return <div class="map-result-list-wrapper">
			<section data-tpl="grid-bas-02">
				<div class="container">
					<ul class="map-result-list">
						{
							items.map( item => <ResultListItem spotInfo={ item } /> )
						}
					</ul>
				</div>
			</section>
		</div>;
	}

}
export default ResultList;



