import { h, Component } from 'preact';

export const TabPane = ( { children, id, selected, ...props } ) =>
	<div class="tab-panel" role="tabpanel" id={ id } aria-expanded={ true }>
		{ children }
	</div>
;

class Tabs extends Component {

	constructor ( props, context ) {
		super( props, context );
		
		this.state = {
			selected : props.selected || 0
		};
	}

	renderNavigation = ( children, selected ) => {
		return <div class="tabs-navigation">
			<ul role="tablist">
				{ children.map( ( child, index ) => {
					const title     = child.attributes.title,
								_selected = selected === index;

					return <li class="tabs-navigation__item" role="presentation">
						<a
							class="tabs-navigation__link"
							href={ '#' + index }
							onClick={ this.handleClick.bind( this, index ) }
							role="tab"
							aria-controls={ index }
							aria-selected={ _selected }>
							{ title }
						</a>
					</li>;
				} ) }
			</ul>
		</div>;
	}

	renderContent = ( children, selected ) => {
		return <div class="tab-content">
			{ children[ selected ] }
		</div>;
	}

	handleClick ( index, e ) {
		e.preventDefault();
		this.setState( {
			selected : index
		} );

		return false;
	}

	render ( { children }, { selected } ) {
		// this.state.selected = selected || this.state.selected;
		return <div data-tbl="tab01" data-module="tab" class="tabs">
			{ this.renderNavigation( children, this.state.selected ) }
			{ this.renderContent( children, this.state.selected ) }
		</div>;
	}

}

export default Tabs;



