import { UI as ACTIONS } from './../actions/constants/Actions.constants.js';

const initialState = {
				started :   false,
				open :      false,
				blocked :   false,
				loading :   false,
				searching : false
			};

export default function ui ( state = initialState, action = { type : '' } ) {
	switch ( action.type ) {
		case ACTIONS.APP_STARTED:
		case ACTIONS.APP_ENDED:
		case ACTIONS.APP_OPEN:
		case ACTIONS.APP_CLOSED:
		case ACTIONS.LOADING_START:
		case ACTIONS.LOADING_FINISH:
			return {
				...state,
				...action.uiState
			};
		default:
			return state;
	}
}



