import { h } from 'preact';
import render from 'preact-render-to-string';

import Button from 'PreactApp/elements/Button.element.preact';

export function infoBoxContentJSX ( info ) {
	return <div class="info-box__wrapper">
		<header class="info-box__header">
			{ info.name }
		</header>
		<div class="info-box__content">
			{ !info.address ? null :
				<div class="info-box__content__address">
					{ info.address.line1 }
					{ info.address.line1 ? <br /> : null }
					{ info.address.line2 }
					{ info.address.line2 ? <br /> : null }
					{info.address.zip} {info.address.city}
				</div>
			}
			{ !info.distance ? null :
				<div class="info-box__content__distance">
					<strong>{ info.distance }</strong>
				</div>
			}
		</div>
		<footer class="info-box__button-bar">
			{ !info.contact || !info.contact.phone ? null :
				<Button
					target={ `tel:${info.contact.phone}` }
					className="color-cta-2">
					Anrufen
				</Button>
			}
			<Button
				target={ `//maps.google.com/maps?daddr=${info.position.lat},${info.position.lon}&amp;ll=` }
				className="color-cta-3"
				newWindow={ true }>
				Navigieren
			</Button>
		</footer>
	</div>;
}

export default function infoBoxContent ( item ) {
	return render( infoBoxContentJSX( item ) );
}



