import { LOCATION_SEARCH as ACTIONS } from './constants/Actions.constants.js';

import { fetchResults } from './Result.actions.js';

function startSearchCreator ( { location, radius } ) {
	return {
		type :   ACTIONS.START_SEARCH,
		search : {
			location,
			radius
		}
	};
}

export function startSearch ( props, state ) {
	return dispatch => {
		dispatch( startSearchCreator( props ) );
		dispatch( fetchResults( state ) );
	};
}



