import { h } from 'preact';
// import openingHoursHelper from 'MapToolApp/helper/OpeningHours.helper';

import classNames from 'classnames';

import OpeningHoursItem from './OpeningHoursItem.preact';

// { spotInfo.openingHours.map( item => <li>{ openingHoursHelper( item ) }</li> )}
// 						</OpeningHours

export default ( { data, className } ) => {
	const items = data.reduce( ( prev, item ) => prev.concat( item.repeat.map( repeats => <OpeningHoursItem repeats={ repeats } opens={ item.opens } closes={ item.closes } /> ) ), [] );
	
	return <div className={ classNames( 'opening-hours', className ) } >
		<ul>
			{ items }
		</ul>
	</div>;
}



