import { FILTER as ACTIONS } from './../actions/constants/Actions.constants.js';

const initialState = [];

export default function filter ( state = initialState, action = { type : '' } ) {
	switch ( action.type ) {
		case ACTIONS.ADD_FILTER:
			return [
				...state,
				action.filter
			];
		case ACTIONS.REMOVE_FILTER:
			return state.filter( v => v !== action.filter );
		default:
			return state;
	}
}



