import { h, render } from 'preact';
import { Provider } from 'preact-redux';

import BaseMapApp from 'MapToolApp/base/BaseMap.app';

import MapGateway from 'MapToolApp/services/gateways/base/MapToolGateway.service';

import bindFilter from 'MapToolApp/helper/BindFilter.helper.preact';

// import store from './SFM.store';

import MapFrame from 'MapToolApp/base/views/MapFrame.preact';
import MapFilter from 'MapToolApp/base/views/framework/MapFilter.preact';

const mapGatewayInstance = new MapGateway( 'shop' );

export default class SFMApp extends BaseMapApp {

	init ( scope, config ) {
		config.gateway = mapGatewayInstance;
		super.init( scope, config );
	}

	renderMap () {
		const filter = this.config.filter ? bindFilter( MapFilter, this.config.filter ) : MapFilter;
		
		render(
			<Provider store={ this.store }>
				<MapFrame MapFilter={ filter } />
			</Provider>
			, this.scope
		);
	}

}



