import { h, Component } from 'preact';

import Collapsible from './../components/Collapsible';

function returnAsString ( obj ) {
	if ( typeof obj === 'string' ) {
		return obj;
	}
	if ( obj.toString ) {
		return obj.toString();
	}
	return obj + '';
}

const WrapWithCollapsible = ( { titleUpdate, transformTitle = returnAsString, ...opts } ) => {
	const defaults = {};
	let collapsible = null;

	function handleTitleUpdate ( title ) {
		if ( collapsible !== null ) {
			collapsible.setTitle( transformTitle( title, collapsible ) );
		}
	}

	if ( titleUpdate ) {
		defaults[ titleUpdate ] = handleTitleUpdate;
	}

	return ( ToWrap ) => class extends Component {
		static defaultProps = defaults;

		render ( { children, ...props } ) {
			return <Collapsible ref={ el => { collapsible = el; } } { ...opts } >
				<ToWrap { ...props }>
					{ children }
				</ToWrap>
			</Collapsible>;
		}

	};
};

export default WrapWithCollapsible;



