import { RESULTS as ACTIONS } from './constants/Actions.constants.js';

import { append as markerDataParser } from 'MapToolApp/helper/MarkerDataParser.helper';

export function resetResults () {
	return {
		type : ACTIONS.RESET_RESULTS
	};
}

export function resultsFetched ( result ) {
	return {
		type :   ACTIONS.RESULTS_FETCHED,
		result
	};
}

export function resultsFetchedError ( error ) {
	return {
		type : ACTIONS.RESULTS_FETCHED_ERROR,
		error
	};
}

export function fetchResults ( { search, filter, gateway } ) {
	return dispatch => { // This is where redux-thunk comes handy
		gateway
			.request( { search, filter } )
			.then( data => {
				const results = {
					items :  markerDataParser( data )
				};

				dispatch( resultsFetched( results ) );
			} )
			.catch( err => {
				dispatch( resultsFetchedError( err ) );
			} );
	};
}



