import { h, Component } from 'preact';
import { connect } from 'preact-redux';

import Header from './framework/MapHeader.preact';
import Footer from './framework/MapFooter.preact';
import TheMap from './framework/Map.preact';

function MapFrame ( { open, MapFilter = () => null, MapHeader = Header, MapFooter = Footer } ) {

	return <section className="map-frame" aria-expanded={ open + '' }>
		<MapHeader className="map-frame__header" />
		<MapFilter className="map-frame__filter" />
		<div class="wrapper--lg">
			<TheMap className="map-frame__map" />
			<MapFooter key={ 1 } className="map-frame__footer" />
		</div>
	</section>;
}

export default connect(
	state => ( { open : state.ui.open } )
)( MapFrame );



