import { h } from 'preact';
import { connect } from 'preact-redux';
import { bindActionCreators } from 'redux';

import * as actions from 'MapToolApp/base/actions/Result.actions';

import Button from 'PreactApp/elements/Button.element.preact';

function handleResetClick ( props, e ) {
	e.preventDefault();
	
	props.resetResults();

	return false;
}

function ResetResultsButton ( props ) {
	return <Button onClick={ handleResetClick.bind( null, props ) }>Mehr erfahren</Button>;
}

export default connect(
	state => state.search,
	() => dispatch => ( { ...bindActionCreators( actions, dispatch ) } )
)( ResetResultsButton );



