import { FILTER as ACTIONS } from './constants/Actions.constants.js';

export function addFilter ( filterName ) {
	return {
		type :   ACTIONS.ADD_FILTER,
		filter : filterName
	};
}

export function removeFilter ( filterName ) {
	return {
		type :   ACTIONS.REMOVE_FILTER,
		filter : filterName
	};
}



