import { h, Component } from 'preact';
import { connect } from 'preact-redux';

import classNames from 'classnames';

import AnimatedMount from 'PreactApp/components/animation/AnimatedMount.preact';

import ResultListTrigger from './../components/ResultListTrigger.preact';
import ResultList from './../components/ResultList.preact';

class MapFrameFooter extends Component {

	constructor ( props ) {
		super( props );

		this.state = {
			isOpen :      false,
			isAvailable : false
		};
	}

	render ( { className, resultsCount }, { isOpen, isAvailable } ) {
		const triggerLabel = 'Ergenisse aufgelistet anzeigen';

		return (
			<footer
				className={ classNames( className, { 'map-frame__footer--open' : isOpen, 'has-results' : resultsCount > 0 } ) }
				aria-expanded={ isOpen }
				>
				<div class="map-result">
					<ResultListTrigger
						label={ triggerLabel }
						onClick={ this.handleTriggerClick.bind( this ) }
						/>
					<ResultList />
				</div>
			</footer>
		);
	}

	hide () {
		this.setState( {
			isOpen : false
		} );
	}
	
	show () {
		this.setState( {
			isOpen : true
		} );
	}

	handleTriggerClick ( e ) {
		if ( this.state.isOpen ) {
			this.hide();
		} else {
			this.show();
		}
	}

}

export default connect(
	state => ( { resultsCount : state.result.items.length } )
)( MapFrameFooter );



