import { MAP as ACTIONS } from './../actions/constants/Actions.constants.js';

const initialState = {
				loading : true
			};

export default function map ( state = initialState, action = { type : '' } ) {
	switch ( action.type ) {
		case ACTIONS.LOADING:
			return {
				...state,
				loading : action.mapState.loading
			};
		default:
			return state;
	}
}



