import { h } from 'preact';
import classNames from 'classnames';

export default ( { target, title, text, children, className, newWindow, ...props } ) =>
	<a
		href={ target }
		title={ title }
		className={ classNames( 'btn', className ) }
		target={ newWindow === true ? '_blank' : false }
		{ ...props }>
		{ text || title || children }
	</a>
;



