function withGoogleMaps ( gmaps ) {
	const InfoBox = require( 'google-maps-infobox-wrapped' )( gmaps );

	return class extends InfoBox {
		div_        = null;
		divClose_   = null;
		divContent_ = null;
		content_    = null;
		padding     = 34;

		constructor ( opts ) {
			super( {
				...opts,
				boxClass :       'map-infobox',
				boxStyle :       '',
				closeBoxURL :    '',
				closeBoxMargin : '',
				pane :           'floatPane'
			} );

			// this.createInfoBoxDiv_();
			// this.div_.classList.add( 'infobox--closed' );
		}

		onAdd () {
			if ( !this.div_ ) {
				this.createInfoBoxDiv_();
			}
		}

		draw () {
			if ( !this.div_ ) {
				this.createInfoBoxDiv_();
			}

			if ( this.closeListener_ === null ) {
				this.closeListener_ = gmaps.event.addDomListener( this.divClose_, 'click', this.getCloseClickHandler_() );
			}

			if ( this.div_.classList.contains( 'info-box--closed' ) ) {
				this.div_.classList.remove( 'info-box--closed' );
			}

			this.div_.classList.add( 'info-box--open' );

			const dimensions  = this.getBoxDimensions(),
						pixPosition = this.getProjection().fromLatLngToDivPixel( this.position_ );

			this.div_.style.left = ( pixPosition.x - ( dimensions.width / 2 ) ) + 'px';
			this.div_.style.top = ( pixPosition.y - dimensions.height - this.padding ) + 'px';

			this.panBox_( this.disableAutoPan_ );
		}

		onRemove () {
			if ( this.div_ ) {
				this.div_.classList.remove( 'info-box--open' );
				this.div_.classList.add( 'info-box--closed' );
			}
		}

		setContent ( content = false ) {
			this.content_ = content;

			if ( this.div_ ) {
				this.setContent_( this.content_ );
			}
		}

		createInfoBoxDiv_ () {
			if ( this.div_ ) {
				return;
			}

			this.div_ = document.createElement( 'div' );
			this.div_.classList.add( 'info-box' );
			this.div_.classList.add( this.boxClass_ );

			this.divClose_ = document.createElement( 'div' );
			this.divClose_.classList.add( 'info-box__close' );

			this.div_.appendChild( this.divClose_ );

			this.divContent_ = document.createElement( 'div' );
			this.divContent_.classList.add( 'info-box__inner' );
			this.div_.appendChild( this.divContent_ );

			this.setBoxStyle_();
			this.div_.style.visibility = 'visible';
			this.div_.style.overflow = '';

			this.getPanes()[ this.pane_ ].appendChild( this.div_ );

			this.setContent_( this.content_ );
		}

		setContent_ ( content ) {
			if ( typeof content === 'string' ) {
				this.divContent_.innerHTML = content;
			} else if ( typeof content.nodeType !== 'undefined' ) {
				this.divContent_.appendChild( content );
			}
		}

		panBox_ ( disablePan ) {
			if ( disablePan ) {
				return;
			}

			

			if ( !( this.getMap() instanceof gmaps.Map ) ) {
				return;
			}

			const map = this.getMap(),
						mapCenter = this.getProjection().fromLatLngToContainerPixel( map.getCenter() ),
						dimensions  = this.getBoxDimensions(),
						pixPosition = this.getProjection().fromLatLngToContainerPixel( this.position_ ),
						boxCenter = {
							x : pixPosition.x,
							y : pixPosition.y - dimensions.height / 2 - this.padding / 2
						},
						xOffset = Math.round( boxCenter.x - mapCenter.x ),
						yOffset = Math.round( boxCenter.y - mapCenter.y );

			if ( xOffset !== 0 && yOffset !== 0 ) {
				map.panBy( xOffset, yOffset );
			}
		}

		getBoxDimensions () {
			const dim = {
				width :  0,
				height : 0
			};

			if ( !this.div_ ) {
				return dim;
			}

			if ( window.getComputedStyle ) {
				const style = window.getComputedStyle( this.div_ );

				dim.width  = parseInt( style.getPropertyValue( 'width' ), 10 );
				dim.height = parseInt( style.getPropertyValue( 'height' ), 10 );
			}

			return dim;
		}

	};
}

export default withGoogleMaps;



