import { h, Component } from 'preact';

import { closeApp } from './../../actions/UI.actions';

import BackNavigation from 'PreactApp/elements/BackNavigation.element.preact';

class MapFrameHeader extends Component {

	constructor ( props, { store } ) {
		super( props );
		
		this._store = store;
	}

	close ( e ) {
		e.preventDefault();

		this._store.dispatch( closeApp() );

		return false;
	}

	shouldComponentUpdate () {
		return false;
	}

	render ( { className } ) {
		return (
			<header className={ className }>
				<BackNavigation title="Karte schließen" onClick={ this.close.bind( this ) } />
			</header>
		);
	}

}

export default MapFrameHeader;



