import { h } from 'preact';

export default ( { id, name, value, type='text', ...props } ) =>
	<input
		id={ id || name }
		name={ name }
		value={ value }
		type={ type }
		class="form-control form-control"
		autocomplete="off"
		autocorrect="off"
		spellcheck="false"
		{ ...props }
		/>
;



