import { h, Component } from 'preact';
import { connect } from 'preact-redux';
import { bindActionCreators } from 'redux';

import classNames from 'classnames';

import * as actions from './../../actions/Filter.actions';

import Icon from 'PreactApp/elements/Icon.element.preact';

@connect(
	null,
	() => dispatch => ( { ...bindActionCreators( actions, dispatch ) } )
)
class FilterIcon extends Component {

	constructor ( props ) {
		super( props );

		this.state = {
			active : props.active || false
		};
	}

	render ( { filter } ) {
		return <Icon
						className={ classNames( filter.icon, 'map-filter__icon', {
							[ filter.colorClass ] : this.state.active,
							'color-background-4' :  !this.state.active,
							'filter--active' :      this.state.active
						} ) }
						onClick={ this.handleClick.bind( this ) }
					/>;
	}

	active () {
		this.setState( {
			active : true
		} );
	}

	inactive () {
		this.setState( {
			active : false
		} );
	}

	handleClick ( e ) {
		if ( this.state.active ) {
			this.props.removeFilter( this.props.filter.id );
			this.inactive();
		} else {
			this.props.addFilter( this.props.filter.id );
			this.active();
		}
	}

}

export default FilterIcon;



