import { h } from 'preact';

import datenames from 'date-names/de';

const weekDays = [ 'SU', 'MO', 'TU', 'WE', 'TH', 'FR', 'SA' ];

function getDaysChained ( days ) {

	const inChain = days.reduce( ( prev, day ) => {
		const dayIndex = weekDays.indexOf( day );

		if ( prev.length - 1 >= 0 && prev[ prev.length - 1 ].current + 1 === dayIndex ) {
			prev[ prev.length - 1 ].to = dayIndex;
		} else {
			prev.push( {
				from : dayIndex
			} );
		}

		prev[ prev.length - 1 ].current = dayIndex;

		return prev;
	}, [] );

	return inChain.map( item => datenames.abbreviated_days[ item.from ] + (
		item.to ?
			'-' + datenames.abbreviated_days[ item.to ] :
			''
		)
	);
}

function Weekly ( { interval, byDay, opens, closes } ) {
	const days = getDaysChained( byDay );

	return <li>
		{ interval === 1 ? '' : `Jede ${ interval }. Woche` }
		{ days.join( ', ' ) }: { opens } - { closes }
	</li>;
}

export default ( { repeats, opens, closes } ) => {
	let Component = null;

	switch ( repeats.rule ) {
		case 'weekly':
			Component = Weekly;
		break;
		// case 'monthly':
		// 	Component = Monthly;
		// break;
		default:
	}

	return <Component { ...repeats } opens={ opens } closes={ closes } />;
};



