import $ from 'jquery';

import { IDENTIFIERS } from './../constants/Identifiers.const';

// import MapFrame from './../framework/MapFrame.preact';
// import FlyoutController from './../components/Flyout.controller';
// import TabsController from './../components/Tabs.controller';

import apps from 'MapToolApp/apps';

class MapToolAppLoader {

	static loadApp () {
		
		$.each( $( IDENTIFIERS.MAPTOOL ), function createInstance () {
			const appToUse = $( this ).data( 'app' );

			if ( !appToUse || typeof apps[ appToUse ] === 'undefined' ) {
				throw new Error( 'Cannot initialise Map Tool Component with given app', appToUse );
			}

			const instance = new apps[ appToUse ]();

			instance.init( this, this.dataset.moduleConfig ? JSON.parse( this.dataset.moduleConfig ) : {} );
		} );

		// $.each( $( IDENTIFIERS.FLYOUT.ROOT ), function createInstance () {
		// 	const controllerInstance = new FlyoutController();

		// 	controllerInstance.init( $( this ) );
		// } );

		// $.each( $( IDENTIFIERS.TABS.ROOT ), function createInstance () {
		// 	const controllerInstance = new TabsController();

		// 	controllerInstance.init( $( this ) );
		// } );
	}

}

export default MapToolAppLoader;



