import { h, Component } from 'preact';
import { connect } from 'preact-redux';

import classNames from 'classnames';

import Measure from 'react-measure';
import debounce from 'debounce';

import BaseMap from './BaseMap.preact';
import GoogleMapController from './../../controller/GoogleMap.controller';

import { finishLoading } from './../../actions/UI.actions';

function withGoogleMaps ( opts ) {
	const controller = new GoogleMapController();

	return ( ToWrap ) => @connect(
		state => ( {
			search : state.search,
			items :  state.result.items,
			filter : state.filter
		} ),
		() => dispatch => ( {
			finishLoading : () => dispatch( finishLoading() )
		} )
	)
	class extends Component {
		mapEl = null;

		componentWillReceiveProps ( next ) {
			controller.checkProps( next );
		}

		shouldComponentUpdate () {
			return false;
		}

		render ( { className, ...props } ) {
			return <Measure onMeasure={ debounce( this.onMeasure.bind( this ), 50 ) } whitelist={ [ 'width', 'height' ] }>
				<ToWrap
					className={ classNames( className, 'google-map' ) }
					setMapElement={ el => { this.mapEl = el; } }
					{ ...props } />
			</Measure>;
		}

		componentDidMount () {
			controller.loadingState()
				.then( () => {
					controller.subscribe( 'map-loaded', this.props.finishLoading );
					controller.init( this.mapEl );
				} );
		}

		onMeasure ( dimensions ) {
			controller.dimensionChanged( dimensions );
		}

	};
}

export default withGoogleMaps()( BaseMap );



