const markerSchema = {
	id :       0,
	position : {},
	title :    ''
};

function checkOwnProps ( props, obj, def = null ) {
	return props.reduce(
		( prev, cur ) => obj.hasOwnProperty( cur ) ? obj[ cur ] : prev,
		typeof def === 'function' ? def() : def );
}

function transform ( item ) {
	const transformed = { ...markerSchema },
				posProps = checkOwnProps( [ 'position', 'location' ], item, {} );
	
	if ( posProps !== false ) {
		transformed.position = {
			lat : parseFloat( checkOwnProps( [ 'lat', 'lt', 'latitude' ], posProps, 0 ) ),
			lng : parseFloat( checkOwnProps( [ 'lon', 'lng', 'ln', 'longitude' ], posProps, 0 ) )
		};
	}

	transformed.title = checkOwnProps( [ 'label', 'title', 'name' ], item, '' );

	transformed.id = checkOwnProps( [ 'id', 'uid' ], item, JSON.stringify( transformed.position ) );
	
	return transformed;
}

export function append ( raw ) {
	return raw.map( item => ( { ...item, marker : transform( item ) }) );
}

export default ( raw ) => {
	if ( !Array.isArray( raw ) ) {
		throw new Error( 'Marker Data Parser: Wrong data format injected' );
	}

	return raw.map( transform );
};



