import { h, Component } from 'preact';

import FormGroupElement from './../elements/form/FormGroup.element.preact';
import LabelElement from './../elements/form/Label.element.preact';
import Select from './Select.preact';

class FormGroupSelect extends Component {

	constructor ( props ) {
		super( props );

		this.valueChange = typeof props.onValueChange === 'function' ? props.onValueChange : val => val;
	}

	render ( { id, name, label, value, selected, disabled, options }, state ) {
		
		id = id || name;

		return <FormGroupElement>
			{ label &&
				<LabelElement for={ name }>{ label }</LabelElement>
			}
			<Select

				id={ id }
				name={ name }
				value={ value }
				disabled={ disabled }
				options={ options }
				selected={ selected }
				
				onValueChange={ this.valueChange }
				
				ref={ el => this.select = el }
				/>
		</FormGroupElement>;
	}

}

export default FormGroupSelect;



