import { UI as ACTIONS } from './constants/Actions.constants.js';

export function startApp () {
	return {
		type :     ACTIONS.APP_STARTED,
		uiState : {
			started : true
		}
	};
}

export function endApp () {
	return {
		type :     ACTIONS.APP_ENDED,
		uiState : {
			started : false
		}
	};
}

export function openApp () {
	return {
		type :     ACTIONS.APP_OPEN,
		uiState : {
			open : true
		}
	};
}

export function closeApp () {
	return {
		type :     ACTIONS.APP_CLOSED,
		uiState : {
			open : false
		}
	};
}

export function startLoading () {
	return {
		type :     ACTIONS.LOADING_START,
		uiState : {
			loading : true,
			blocked : true
		}
	};
}

export function finishLoading () {
	return {
		type :     ACTIONS.LOADING_FINISH,
		uiState : {
			loading : false,
			blocked : false
		}
	};
}

export function startSearching () {
	return {
		type :    ACTIONS.SEARCHING_START,
		uiState : {
			searching : true
		}
	}
}

export function finishSearching () {
	return {
		type :    ACTIONS.SEARCHING_FINISH,
		uiState : {
			searching : false
		}
	}
}



