import BaseGatewayService from './BaseGateway.service';

import $ from 'jquery';

const xmlURLs = {
				shop : '/assets/Filialen.xml'
			},
			map = {
				storecode :            'id',
				name :                 'name',
				latitude :             'position.lat',
				longitude :            'position.lon',
				addressline1 :         'address.line1',
				addressline2 :         'address.line2',
				city :                 'address.city',
				postalcode :           'address.zip',
				mainphone :            'contact.phone',
				faxphone :             'contact.fax',
				homepage :             'contact.web',
				hours :                'openingHours',
				cBrandscarriedSTRING : 'brandsCarried'
			},

			weekDays = [ 'SU', 'MO', 'TU', 'WE', 'TH', 'FR', 'SA' ];

function shortenWeekDayName ( base = weekDays ) {
	return index => {
		return base[ index ] ? base[ index ] : '';
	};
}

function normalizeOpeningHours ( input ) {
	const openingTimes  = input.split( ',' ),
				objectified   = openingTimes
					.reduce( ( prev, cur ) => {
						const hours = cur.split( ':' ),
									hoursFormatted = {
										day :    hours[ 0 ] - 1,
										opens :  `${hours[ 1 ]}:${hours[ 2 ]}`,
										closes : `${hours[ 3 ]}:${hours[ 4 ]}`
									};

						if ( !prev[ hoursFormatted.opens ] ) {
							prev[ hoursFormatted.opens ] = {};
						}
						if ( !prev[ hoursFormatted.opens ][ hoursFormatted.closes ] ) {
							prev[ hoursFormatted.opens ][ hoursFormatted.closes ] = [];
						}

						prev[ hoursFormatted.opens ][ hoursFormatted.closes ].push( shortenWeekDayName()( hoursFormatted.day ) );

						return prev;
					}, {} ),
				normalized = Object.keys( objectified ).reduce( ( prev, opens ) => {
					const current = [];

					Object.keys( objectified[ opens ] ).forEach( closes => {
						current.push( {
							opens,
							closes,
							repeat : [ {
								rule :     'weekly',
								interval : 1,
								byDay :    objectified[ opens ][ closes ]
							} ]
						} );
					} );

					return prev.concat( current );
				}, [] );

	return normalized;
}

function normalize ( what, key ) {
	switch ( key ) {
		case 'openingHours':
			return normalizeOpeningHours( what );
		break;
		default:
			return what;
	}
}

function mapToProps ( key, value, props = {} ) {
	if ( typeof map[ key.toLowerCase() ] !== 'undefined' ) {
		const propKey = map[ key.toLowerCase() ].split( '.' ),
					last = propKey[ propKey.length - 1 ];

		propKey.reduce( ( prev, cur ) => {
			if ( typeof prev[ cur ] === 'undefined' || last === cur ) {
				prev[ cur ] = last === cur ? normalize( value, cur ) : {};
			}

			return prev[ cur ];
		}, props );

		return props;
	} else {
		return {
			[ key.toLowerCase() ] : value
		};
	}
}

function parseXML ( xml ) {
	const $xml = $( xml ),
				result = [];

	$xml.find( 'NewsItemRow' ).each( ( k, item ) => {
		let obj = {};

		for ( let i = 0; i < item.children.length; i++ ) {
			const { nodeName, textContent } = item.children[ i ];

			mapToProps( nodeName, textContent, obj );
		}

		result.push( obj );
	} );

	return {
		error :  false,
		result
	};
}

function loadXML () {
	if ( xmlURLs[ this.type ] ) {
		return new Promise( ( resolve, reject ) => {
			$
				.get(
					xmlURLs[ this.type ],
					( data ) => {
						const parsed = parseXML( data );

						if ( parsed.error === true ) {
							reject( 'Error during parsing of XML document' );
						} else {
							resolve( parsed.result );
						}
					}
				)
				.fail( ( err ) => {
					reject( err );
				} );
		} );
	} else {
		return Promise.reject( 'No type was specified' );
	}
}

class XMLGatewayService extends BaseGatewayService {
	constructor ( type ) {
		super( type );

		this.xml = null;
		// this.xmlRequest = loadXML.call( this );
	}

	request ( params ) {
		return loadXML.call( this ).then( ( data ) => {
			this.xml = data;

			return data;
		} );
		// return Promise.reject( 'XML File Connector is deprecated' );
	}

}

export default XMLGatewayService;



