import { h } from 'preact';

import MapOverlay from './../components/MapOverlay.preact';
import GoogleMapConnector from './../components/GoogleMapConnector.preact';

export default ( { className } ) =>
	<div className={ className } >
		<MapOverlay className="map-frame__overlay" />
		<GoogleMapConnector className="map-frame__map-container" />
	</div>
;



