import { h } from 'preact';

import Subheadline from 'PreactApp/elements/Subheadline.element.preact';
import Button from 'PreactApp/elements/Button.element.preact';
import OpeningHours from 'PreactApp/components/OpeningHoursList.preact';

export default ( { spotInfo } ) => {
	// console.log( 'RESULT LIST ITEM');
	return <li class="map-result-item" id={ spotInfo.id }>
		<div class="map-result-item__info">
			<header class="map-result-item__headline">
				<Subheadline text={ spotInfo.name } />
			</header>
			<div class="map-result-item__details">
				<div class="map-result-item__address">
					{ spotInfo.address.line1 }<br />
					{ spotInfo.address.line2 }{ spotInfo.address.line2 ? <br /> : null }
					{ spotInfo.address.zip } { spotInfo.address.city }{ spotInfo.distance ? <br /> : null }
					{ spotInfo.distance ? <div class="map-result-item__distance"><strong>{ spotInfo.distance }</strong></div> : null }
				</div>
				{ !spotInfo.openingHours ? null :
					<OpeningHours data={ spotInfo.openingHours } className="map-result-item__opening-hours" />
				}
			</div>
		</div>
		<footer class="map-result-item__links">
			<ul>
				{ !window.UserAgent.isMobile ? null :
					<li class="map-result-item__link map-result-item__link--call">
						<Button
							target="#"
							title="Anrufen"
							className={ [ 'color-cta-2' ] }
							/>
					</li>
				}
				<li class="map-result-item__link map-result-item__link--navigate">
					<Button
						target={ `//maps.google.com/maps?daddr=${spotInfo.position.lat},${spotInfo.position.lon}&amp;ll=` }
						title="Navigieren"
						newWindow={ true }
						className={ [ 'color-cta-3' ] }
						/>
				</li>
			</ul>
		</footer>
	</li>
};



