import { h } from 'preact';

import AutocompleteSuggestionList from './AutocompleteSuggestionList.preact';

function beginsWithZip ( text ) {
	return /[\d]{5}\s\D+/.test( text );
}

class AutocompleteRequireZIPSuggestionList extends AutocompleteSuggestionList {

	constructor ( props ) {
		props.needsValidSuggestion = true;
		super( props );
	}

	selectSuggestion ( text ) {
		if ( beginsWithZip( text ) ) {
			super.selectSuggestion( text );
		} else {
			this.getSuggestions( text );
		}
	}

}

export default AutocompleteRequireZIPSuggestionList;



