// import MapType from './../../../base/MapType';

import $ from 'jquery';

const _type = new WeakMap();

class BaseGatewayService {

	constructor ( type = null ) {
		this.type = type; // new MapType( type );
	}

	set type ( type ) {
		_type.set( this, type );
	}

	get type () {
		return _type.get( this );
	}

	buildRequestURL ( method, params ) {
		return '{{API_URL}}/' + [ this.type, method ].join( '/' );
	}

	request ( params ) {
		params.radius = parseFloat( params.radius );
		params.location = new MapPoint( params.location );

		const url = this.buildRequestURL( params.method );
		console.log( url );
		return Promise.reject( 'Should be overwritten in child class' );
		// const url = this.buildRequestURL();

		// if ( !url ) {
		// 	return Promise.reject( 'Not a valid URL.' );
		// }

		// return new Promise( ( resolve, reject ) => {
		// 	$
		// 		.get( url, ( result ) => {
		// 			resolve( result );
		// 		} )
		// 		.fail( ( error ) => {
		// 			reject( error );
		// 		} );
		// } );
	}

	getNearestPointTo ( params ) {
		params.type     = this.type;
		params.method   = 'point';

		return this.request( params );
	}

	getPointForLocation ( params ) {
		params.radius = 0;

		return this.getNearestPointTo( params );
	}

	getPointsForLocation ( params ) {
		params.type   = this.type;
		params.method = 'points';

		return this.request( params );
	}

	getPointsForRoute ( params ) {
		params.type   = this.type;
		params.method = 'points';
		params.findBy = 'route';

		return this.request( params );
	}

	getPointsForArea ( params ) {
		params.type   = this.type;
		params.method = 'points';
		params.findBy = 'area';

		return this.request( params );
	}

	getInfoForArea ( params ) {
		params.type   = this.type;
		params.method = 'areainfo';

		return this.request( params );
	}

}

export default BaseGatewayService;



