import { h } from 'preact';

export default ( { children, ...props } ) => {
	let colClasses = [ 'col' ];

	[ 'xs', 'sm', 'md', 'lg', 'xl', 'xxl' ].forEach( ( val ) => {
		if ( props.hasOwnProperty( val ) ) {
			colClasses.push( `col-${val}-${props[ val ]}` );
			delete props[ val ];
		}
	} );

	if ( props && props.class ) {
		colClasses = colClasses.concat( props.class.split( ' ' ) );
		delete props.class;
	}

	return <div class={ colClasses.join( ' ' ) } { ...props }>
		{ children }
	</div>;
};



