import { h, Component } from 'preact';
import { connect } from 'preact-redux';

import classNames from 'classnames';

import { LocationSearchWrapped as LocationSearch } from './../components/LocationSearch.preact';
import Filter from './../components/Filter.preact';

export default ( { bem, className, radiusOptions, children, filter } ) => {
	if ( !children || !children.length ) {
		children = [ <LocationSearch /> ]; // eslint-disable-line no-param-reassign
		if ( filter ) {
			children.push( <Filter filter={ filter } /> );
		}
	}

	return <div className={ classNames( className, 'color-background-3' ) }>
		{ children }
	</div>;
};



