import { h } from 'preact';

import MapFilter from 'MapToolApp/base/views/framework/MapFilter.preact';
import LocationSearch from 'MapToolApp/base/views/components/LocationSearch.preact';
import Tabs, { TabPane } from 'MapToolApp/base/views/components/Tabs.preact';

export default ( props ) =>
	<MapFilter { ...props }>
		<Tabs>
			<TabPane title="Stationen finden">
				<LocationSearch />
			</TabPane>
			<TabPane title="Tab 02">
				<div>This is Tab02 content!</div>
			</TabPane>
		</Tabs>
	</MapFilter>
;



