
function withGoogleMaps ( gmaps ) {

	return class extends gmaps.OverlayView {
		element        = null;
		map            = null;
		bounds         = null;
		paneStyle      = {};
		padding        = 1000;
		changeListener = null;

		constructor ( map = null ) {
			super();

			if ( map !== null ) {
				this.map    = map;

				this.setMap( this.map );
			}
		}

		onAdd () {
			const panes  = this.getPanes(),
						target = panes.markerLayer;

			if ( this.element !== null ) {
				return;
			}

			this.element                  = document.createElement( 'div' );
			this.element.style.position   = 'absolute';
			this.element.style.zIndex     = 1000;

			this.element.classList.add( 'map-overlay' );
			this.element.classList.add( 'overlay--closed' );

			this.changeListener = gmaps.event.addListener( this.getMap(), 'center_changed', () => {
				this.draw();
			} );

			// const projection = this.getProjection(),
			// 			sw = projection.fromLatLngToDivPixel( this.map.getBounds().getSouthWest() ),
			// 			ne = projection.fromLatLngToDivPixel( this.map.getBounds().getNorthEast() );

			// this.element.style.left   = ( sw.x - 100 ) + 'px';
			// this.element.style.top    = ( ne.y - 100 ) + 'px';
			// this.element.style.width  = ( ne.x - sw.x + 200 ) + 'px';
			// this.element.style.height = ( sw.y - ne.y + 200 ) + 'px';

			target.appendChild( this.element );

			// this.paneStyle = {
			// 	...target.style
			// };
			// target.style.zIndex = 1000;
		}

		draw () {
			const projection = this.getProjection(),
						sw = projection.fromLatLngToDivPixel( this.map.getBounds().getSouthWest() ),
						ne = projection.fromLatLngToDivPixel( this.map.getBounds().getNorthEast() );

			this.element.classList.remove( 'overlay--closed' );
			this.element.classList.add( 'overlay--open' );

			this.element.style.left   = ( sw.x - this.padding ) + 'px';
			this.element.style.top    = ( ne.y - this.padding ) + 'px';
			this.element.style.width  = ( ne.x - sw.x + this.padding * 2 ) + 'px';
			this.element.style.height = ( sw.y - ne.y + this.padding * 2 ) + 'px';
		}

		onRemove () {
			// const parent = this.element.parentNode;
			
			this.element.classList.remove( 'overlay--open' );
			this.element.classList.add( 'overlay--closed' );

			gmaps.event.removeListener( this.changeListener );

			// parent.removeChild( this.element );
			// this.element = null;

			// parent.style.zIndex = this.paneStyle.zIndex;
		}

	};

}

export default withGoogleMaps;



