import { h, Component } from 'preact';

export default function BindFilter ( Wrapped, filter ) {

	return class extends Component {

		constructor ( props ) {
			super( props );
			this.state = { filter };
		}

		render ( props ) {
			return <Wrapped filter={ this.state.filter } { ...props } />;
		}

	};

}



