import { h, Component } from 'preact';

class BaseMap extends Component {

	shouldComponentUpdate () {
		return false;
	}

	render ( { setMapElement = () => {}, ...props } ) {
		return <div ref={ setMapElement } { ...props } />;
	}

}

export default BaseMap;



