import { h, Component } from 'preact';

import TransitionGroup from 'preact-transition-group';

const WrapWithTransitionGroup = ( { showProp = 'show', ...opts } ) => {
	opts.component = 'div';

	function getShowProp ( props ) {
		if ( showProp && props.hasOwnProperty( showProp ) ) {
			const show = props[ showProp ];

			delete props[ showProp ];
			return show;
		}
		return false;
	}

	return ( ToWrap ) => class extends Component {

		render ( { children, ...props } ) {
			return <TransitionGroup { ...opts } >
				{ getShowProp( props ) === false ? null :
					<ToWrap { ...props }>
						{ children }
					</ToWrap>
				}
			</TransitionGroup>;
		}
	};
};

export default WrapWithTransitionGroup;



