import { h, Component } from 'preact';

// import Collapse from 'react-collapse';
import { addEndEventListener, removeEndEventListener } from './../helper/TransitionEvents.helper';

import classNames from 'classnames';

import fasterdom from 'fasterdom';

function headerTransitionEnd ( e ) {
	if ( e.target !== this.headerElement || e.propertyName !== 'height' ) {
		return;
	}

	if ( this.props.collapseHeader ) {
		this.setState( {
			headerHeight : false
		} );
	}
}

function setHeaderElement ( el ) {
	this.headerElement = el;
}

function contentTransitionEnd ( e ) {
	if ( e.target !== this.contentElement || e.propertyName !== 'height' ) {
		return;
	}

	const stateEnd = {
					duringAnimation : false,
					contentHeight :   false
				};

	this.setState( stateEnd );
}

function setContentElement ( el ) {
	this.contentElement = el;
}

class Collapsible extends Component {

	static defaultProps = {
		startOpened :          false,
		collapseHeader :       false,
		keepCollapsedContent : false,
		title :                '{HEADER TITLE}'
	};

	constructor ( props ) {
		super( props );

		this.state = {
			contentOpened :   props.startOpened,
			contentHeight :   false,
			title :           props.title,
			duringAnimation : false
		};

		if ( props.collapseHeader ) {
			this.state.headerOpened = !props.startOpened;
			this.state.headerHeight = false;
		}
	}

	render ( { collapseHeader, children, ...props }, { duringAnimation, contentOpened, contentHeight, headerOpened, headerHeight, title } ) {
		const contentStyles = { height : '' },
					headerStyles  = { height : '' };

		if ( contentHeight !== false ) {
			contentStyles.height = contentHeight;
		}

		if ( headerHeight !== false ) {
			headerStyles.height = headerHeight;
		}

		return <div
			className={
				classNames(
					'collapsible',
					{
						'collapsible--header-collapsible' :          collapseHeader,
						'collapsible--expanded' :                    contentOpened,
						'collapsible--header-expanded' :             headerOpened,
						'during-animation' :                         duringAnimation,
						[ `during-animation--${duringAnimation}` ] : duringAnimation !== false
					}
				)
			}>
			<header
				className="collapsible__header"
				ref={ setHeaderElement.bind( this ) }
				style={ headerStyles }
				onClick={ this.handleHeaderClick.bind( this ) }>
				<div
					ref={ el => { this.headerInnerElement = el; } }
					className="collapsible__header-inner">
					{ title }
				</div>
			</header>
			<div
				className="collapsible__content"
				ref={ setContentElement.bind( this ) }
				style={ contentStyles }>
				<div
					className="collapsible__content-inner"
					ref={ el => { this.contentInnerElement = el; } }>
					{ children }
				</div>
			</div>
		</div>;
	}

	componentDidMount () {
		if ( this.headerElement ) {
			addEndEventListener( this.headerElement, headerTransitionEnd.bind( this ) );
		}
		if ( this.contentElement ) {
			addEndEventListener( this.contentElement, contentTransitionEnd.bind( this ) );
		}
	}

	componentWillUnmount () {
		removeEndEventListener( this.headerElement, headerTransitionEnd.bind( this ) );
		removeEndEventListener( this.headerElement, contentTransitionEnd.bind( this ) );
	}

	componentDidUpdate () {
		// if ( this.state.contentOpened === false && this.state.contentHeight !== false ) {
		// 	this.setState( {
		// 		contentHeight : false
		// 	} );
		// }
		// if ( this.props.collapseHeader && this.state.headerOpened === false && this.state.headerHeight !== false ) {
		// 	this.setState( {
		// 		headerHeight : false
		// 	} );
		// }
	}

	handleHeaderClick ( e ) {
		e.preventDefault();

		this.toggle();

		return false;
	}

	setTitle ( title ) {
		this.setState( { title } );
	}

	toggle () {
		if ( this.state.contentOpened ) {
			this.collapse();
		} else {
			this.expand();
		}
	}

	expand () {
		this.setState( {
			duringAnimation : 'open',
			contentOpened :   true,
			contentHeight :   this.contentInnerElement.offsetHeight
		} );
		if ( this.props.collapseHeader ) {
			this.collapseHeader();
		}
	}

	expandHeader () {
		this.setState( {
			headerOpened : true,
			headerHeight : this.headerInnerElement.offsetHeight
		} );
	}

	collapse () {
		this.setState( {
			duringAnimation : 'close',
			contentOpened :   false,
			contentHeight :   this.contentInnerElement.offsetHeight
		} );
		fasterdom.onFrame( () => {
			this.setState( {
				contentHeight : false
			} );
		}, 1 );
		if ( this.props.collapseHeader ) {
			this.expandHeader();
		}
	}

	collapseHeader () {
		this.setState( {
			headerOpened : false,
			headerHeight : this.headerInnerElement.offsetHeight
		} );
		fasterdom.onFrame( () => {
			this.setState( {
				headerHeight : false
			} );
		}, 1 );
	}

}

export default Collapsible;



