import { h, Component } from 'preact';

import classNames from 'classnames';

import Collapse from 'react-collapse';

import wrapWithTransitionGroup from 'PreactApp/helper/WrapWithTransitionGroup.preact';

class AnimatedMount extends Component {

	constructor ( props ) {
		super( props );
		this.state = {
			isOpened : false
		};
	}

	componentWillAppear ( cb ) {
		setTimeout(
			this.open.bind( this, cb ),
			1
		);
	}

	componentDidAppear

	componentWillEnter ( cb ) {
		setTimeout(
			this.open.bind( this, cb ),
			1
		);
		
	}

	componentDidEnter

	componentWillLeave ( cb ) {
		this.close( cb );
	}

	componentDidLeave

	render ( { children, className, ...props }, { isOpened, onRest } ) {
		return <Collapse
			isOpened={ isOpened }
			onRest={ onRest }
			className={ classNames( className, 'animation-wrapper' ) }
			{ ...props }>
			{ children }
		</Collapse>;
	}

	open ( cb = () => {} ) {
		this.setState( {
			isOpened : true,
			onRest :   cb
		} );
	}

	close ( cb = () => {} ) {
		this.setState( {
			isOpened : false,
			onRest :   cb
		} );
	}

}

export default wrapWithTransitionGroup( { showProp : 'mount' } )( AnimatedMount );



