import { h, Component } from 'preact';
import { connect } from 'preact-redux';
import { bindActionCreators } from 'redux';

// import reducer from './../../reducers/LocationSearch.reducer';
import * as actions from 'MapToolApp/base/actions/LocationSearch.actions';

import Row from 'PreactApp/elements/grid/Row.element.preact';
import Col from 'PreactApp/elements/grid/Col.element.preact';
import Form from 'PreactApp/elements/form/Form.element.preact';

import FormGroupInput from 'PreactApp/components/FormGroupInput.preact';
import FormGroupSelect from 'PreactApp/components/FormGroupSelect.preact';

import wrapWithCollapsible from 'PreactApp/helper/WrapWithCollapsible.preact';

const defaultRadiusOptions = [
				{ text : '5 km', value : 5 },
				{ text : '10 km', value : 10 },
				{ text : '25 km', value : 25 },
				{ text : '50 km', value : 50 },
				{ text : '100 km', value : 100 }
			];

function optionByValue ( options, value ) {
	return options.reduce( ( prev, cur, i ) => cur.value === value ? i : prev, -1 );
}

@connect(
	state => state.search,
	() => dispatch => ( { ...bindActionCreators( actions, dispatch ) } )
)
class LocationSearch extends Component {
	static defaultProps = {
		location :      null,
		radiusOptions : defaultRadiusOptions,
		radius :        defaultRadiusOptions[ 2 ],
		onUpdate :      () => {},
		onSearch :      () => {}
	}
	
	constructor ( props, { store } ) {
		super( props );

		this._store = store;

		this.state = {
			location : props.location,
			radius :   props.radius,
			selected : optionByValue( props.radiusOptions, props.radius.value ),
			disabled : false
		};
	}

	render ( { radiusOptions }, { location, radius, disabled, selected } ) {
		const btnDisabled = disabled || !location || selected === -1;

		return (
			<section class="map-search">
				<Form id="location-form" onSubmit={ this.search.bind( this ) }>
					<Row>
						<Col md={6} xs={8} class="location">
							<FormGroupInput
								autocomplete={ {
									gateway :      'location',
									emptyMessage : 'Es wurden keine Vorschläge gefunden'
								} }
								inlineEditing={ true }
								name="location-search"
								value={ location }
								label="Ort"
								disabled={ disabled }
								onTextChange={ this.handleLocationChange.bind( this ) }
								/>
						</Col>
						<Col md={3} xs={4} class="radius">
							<FormGroupSelect
								name="location-radius-select"
								value={ radius.value }
								label="Radius"
								options={ radiusOptions }
								disabled={ disabled }
								selected={ selected }
								onValueChange={ this.handleRadiusChange.bind( this ) } />
						</Col>
						<Col md={3} xs={12} class="button">
							<button class="btn color-cta-4 btn--full-width" disabled={ btnDisabled }>
								Los geht's
							</button>
						</Col>
					</Row>
				</Form>
			</section>
		);
	}

	search ( e ) {
		e.preventDefault();
		// this.setState( {
		// 	disabled : true
		// } );
		if ( !this.state.location || !this.state.radius.value ) {
			return false;
		}

		if ( typeof this.props.onSearch === 'function' ) {
			this.props.onSearch( this.state );
		}
		this.props.startSearch( this.state, this._store.getState() );
		
		return false;
	}

	handleLocationChange ( e ) {
		if ( this.state.disabled ) {
			return;
		}
		this.setState( {
			location : typeof e === 'string' ? e : e.target.value
		} );
	}

	handleRadiusChange ( val ) {
		if ( this.state.disabled ) {
			return;
		}
		const selected = optionByValue( this.props.radiusOptions, val );

		this.setState( {
			radius : this.props.radiusOptions[ selected ],
			selected
		} );
	}
	
}

export const LocationSearchWrapped = wrapWithCollapsible( {
				titleUpdate :    'onSearch',
				transformTitle : ( locState, collapsible ) => {
					collapsible.collapse();
					return <h3>{ `${locState.location}, im Umkreis von ${locState.radius.text}` }</h3>;
				},
				
				collapseHeader :       true,
				startOpened :          true
			} )( LocationSearch );

export default LocationSearch;



