import BaseModule from './../Base.module';
import MapToolAppLoader from './services/MapToolAppLoader.service';

/**
 * Module for tables to creates a responsive table with fixed first column
 * @extends {BaseModule}
 */
class MapToolModule extends BaseModule {

	init () {
		super.init();

		MapToolAppLoader.loadApp();
	}
}

export default MapToolModule;



