import { LOCATION_SEARCH as ACTIONS } from './../actions/constants/Actions.constants.js';

const initialState = {
				location :  null,
				radius :    {
					text :  '25 km',
					value : 25
				}
			};

export default function search ( state = initialState, action = { type : '' } ) {
	switch ( action.type ) {
		case ACTIONS.START_SEARCH:
			const { location, radius } = action.search;
			
			return {
				...state,
				location :  location,
				radius :    radius
			};
		default:
			return state;
	}
}



