import $ from 'jquery';
const URL = {
	host : '',
	path : '/energiekaufhaus/rs/regionalisierung/autocomplete?limit=100&ortEingabe='
};

function beginsWithZip ( text ) {
	return /[\d]{5}\s\D+/.test( text );
}

function getSuggestions ( query ) {
	let checkedQuery = query.split( ' ' ).join( '+' );

	if ( beginsWithZip( query ) ) {
		checkedQuery = checkedQuery.substring( 6 );
	}

	return new Promise( ( resolve, reject ) => {

				$.get( URL.host + URL.path + checkedQuery, ( result ) => {
					const suggestions = [];

					$.each( result, ( index, value ) => {
						if ( value.zip ) {
							suggestions.push( value.zip + ' ' + value.city );
						} else {
							suggestions.push( value.city );
						}
					} );

					resolve( suggestions );
				} )
						.fail( ( error ) => {
							reject( error );
						} );

			}
	);
}

export default {
	getSuggestions :          getSuggestions,
	checkSelectedSuggestion : ( suggestion ) => {

		return new Promise( ( resolve, reject ) => {

			// check if there is a zip code in suggestion
			if ( beginsWithZip( suggestion ) ) {
				resolve( suggestion );
			}

			getSuggestions( suggestion ).then( ( suggestions ) => {
				resolve( suggestions[ 0 ] );
			} ).catch( ( err ) => {
				reject( err );
			} );

		} );
	}
}


